
body{
background-color: darkorange;
}
.popupparentnone{
    display: none; /* Hidden by default */
   
}
.popupparentblock{
    display: block; /* Hidden by default */
    position: fixed; /* Stay in place */
    z-index: 2; /* Sit on top */
    padding-top: 100px; /* Location of the box */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}


.popupcontainer{
    display: flex;
    width:100%;
    height: 98vh;
    justify-content: center;
    align-content: center;
    
    
}
.popup{
width:400px;
height: 400px;
background-color: teal;
position: relative;



}
.addbutton{
    border:5px solid green;
    font-family: cursive;
    font-style: normal;
    font-size: 1.5rem;
    text-align: center;
    background-color:mediumspringgreen;
}
.addbutton:hover{
    cursor: pointer;
    background-color: mediumturquoise;
}
.todobutton{
    
    background-color: #F62110;
    font-size: medium;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    text-align: center;
    height: 40px;
    width: 180px;
    

}
.todobutton:hover{
    background-color: #C70039 ;
    
}
.textbox{
    
   
    font-size:medium;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    text-align: left;
    height: 40px;
    padding:20px;
    background-color: palegreen;
    color: black;
    width: 180px;
    margin-bottom: 50px;
    box-sizing: border-box;
    
}
.listcontainer{
display: flex;
justify-content: center;
}

.todolist{
    border:2px solid #900C3F;
    border-radius: 25px;
    background-color: #581845 ;
    color:white;
    font-family: 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    text-align: center;
    padding:5px;
    width: 80%;
    align-self: center;
    margin: 5px;
    position: relative;
    
}
.trashicon{
    position:absolute;
    right:0px;
    cursor: pointer;
    
    
}
.faTrash{
    margin:0 5px;
    font-size: 15px;
}
.iteminput{
    background: transparent;
    outline: none;
    border: none;
    color:white;
}
.windowClose{
    cursor: pointer;
   background-color: blue;
    right: 0px;
    border: none;
    border-bottom:2px solid black;
    width: 100%;
    height: 30px;
    flex:1;
    padding-top: 0px;
    margin:auto
}
.iconclose{
    position: absolute;
    right: 0px;
    background: transparent;
    
    
}

