

*{
    margin:2px;
}
.navcontainer{
    font-size: 24px;
    color:white;
    text-align: center;
    height: 98vh;
    display: flex;
    flex-direction: column;
    
}

.flex-header{
    
    padding-top: 3rem;
    padding-bottom: 3rem;
    background-color: blue;
}

.flex-main{
    display: flex;
    flex:1;
}

.flex-nav{
    background-color: burlywood;
    padding-top: 3rem;
    flex:1 1 5rem
}
.flex-article{
    background-color: cadetblue;
    padding-top: 3rem;
    flex:3 3;
}
.flex-aside{
    background-color: burlywood;
    padding-top: 3rem;
    flex:1 1 5rem;
}
.flex-footer{
    padding-top: 3rem;
    padding-bottom: 3rem;
    background-color: blue;
    
}